"use client";

import { useState } from "react";
import ConfirmDeleteModal from "../../../../components/global/ConfirmDeleteModal/ConfirmDeleteModal";

export default function MyEventsList({ events = [] }) {
  const [deleteEvent, setDeleteEvent] = useState(null);

  return (
    <>
      {events.map((e) => (
        <div key={e.id} className="list-card">
          <img
            src={e.bannerImage || "/images/event-placeholder.png"}
            className="list-img"
          />

          <div className="list-info">
            <h6>{e.title}</h6>
            <span className={`status-badge ${e.status?.toLowerCase()}`}>
              {e.status}
            </span>
          </div>

          <div className="list-actions">
            <button className="edit-btn">Edit</button>
            <button className="menu-btn" onClick={() => setDeleteEvent(e)}>
              ⋮
            </button>
          </div>
        </div>
      ))}

      <ConfirmDeleteModal
        open={!!deleteEvent}
        onClose={() => setDeleteEvent(null)}
        onConfirm={() => setDeleteEvent(null)}
      />
    </>
  );
}
