"use client";

import { useState } from "react";
import ConfirmDeleteModal from "../../../../components/global/ConfirmDeleteModal/ConfirmDeleteModal";
import "./"

export default function MyEventsGrid({ events = [] }) {
  const [menuOpen, setMenuOpen] = useState(null);
  const [deleteEvent, setDeleteEvent] = useState(null);

  return (
    <>
      <div className="row g-4">
        {events.map((e) => (
          <div key={e.id} className="col-xl-4 col-lg-6 col-md-6">
            <div className="event-card">
              
              {/* IMAGE */}
              <div className="card-img-wrapper">
                <img
                  src={e.bannerImage || "/images/event-placeholder.png"}
                  alt={e.title}
                />
                <span className={`status-badge ${e.status?.toLowerCase()}`}>
                  {e.status}
                </span>
              </div>

              {/* BODY */}
              <div className="event-body">
                <h6 title={e.title}>{e.title}</h6>

                <div className="event-actions">
                  <button className="edit-btn">Edit</button>

                  <div className="menu-wrapper">
                    <button
                      className="menu-btn"
                      onClick={() =>
                        setMenuOpen(menuOpen === e.id ? null : e.id)
                      }
                    >
                      ⋮
                    </button>

                    {menuOpen === e.id && (
                      <div className="menu-dropdown">
                        <button onClick={() => navigator.clipboard.writeText(location.href)}>
                          Copy URL
                        </button>
                        <button>Duplicate</button>
                        <button
                          className="text-danger"
                          onClick={() => {
                            setDeleteEvent(e);
                            setMenuOpen(null);
                          }}
                        >
                          Delete
                        </button>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* DELETE CONFIRM */}
      <ConfirmDeleteModal
        open={!!deleteEvent}
        onClose={() => setDeleteEvent(null)}
        onConfirm={() => {
          console.log("delete", deleteEvent?.id);
          setDeleteEvent(null);
        }}
      />
    </>
  );
}
